body{
    margin:0;
    padding: 0%;
    box-sizing: border-box;
}
.form{
    width: 100%;
    text-align: center;
    height: 50vh;
    color: rgb(95, 92, 92);
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
    padding: 100px 0 0 0;

    
}
form{
    width: 30vw;
    height: 35px;
    min-width: 250px;
    border: 2px solid rgb(82, 174, 211);
    border-radius: 100px;
    margin: 0px auto;
    /* padding: 250px 0 0 0; */
    margin-top: 30px;
    position: relative;
    overflow: hidden;

    
}
label{
    color: rgb(255, 255, 255);
    font-size: 30px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 1000;
    color: rgb(247, 244, 248);
    /* background: #e7e5e4; */
    letter-spacing: 2px;
    text-shadow: 5px 3px 11px #7676768c, 8px 5px 10px #0000009f;

}
input{
    position: absolute;
    top: -1px;
    left: 0;
    right: 0;
    bottom: 1px;
    width: 100%;
    height: 35px;
    border: none;
    border-radius: 100px;
    outline: none;
    border: none;
    padding: 0 0 0 10px;
    color: gray;
    
}
button{
    position: absolute;
    right: 1px;
    background:rgba(143, 85, 170, 0.589);
    top: 1px;
    bottom: 1px;
    width: 25%;
    min-width: 75px;
    border: none;
    border-radius: 100px;
    color: rgb(242, 245, 247);
}
